<template>
  <!--分配角色-->

  <el-row :gutter="50">
    <el-col :span="24">
      <el-dialog :title="dialog.title" :visible="dialog.visible" :fullscreen="true"
                 :ref="refs.dialog"
                 @close="close" @open="afterOpen" width="90%">
        <div style="height: 700px">
          <!--left-->
          <el-col :span="12">
            <el-card shadow="never">
              <div slot="header">
                <span>未分配角色</span>
              </div>
              <el-header>
                <el-row>
                  <el-col :span="24">
                    <el-form label-position="left" :inline="refs.inline" :model="leftSearchForm"
                             :ref="refs.leftForm"
                             label-width="80px">
                      <el-col :span="24">
                        <el-form-item label="角色名称">
                          <el-input v-model="leftSearchForm.roleName"
                                    placeholder="请输入角色名称"
                                    style="width:212px"></el-input>
                        </el-form-item>
                      </el-col>
                      <el-col :span="24">
                        <el-form-item label="归属系统">
                          <!-- <el-checkbox-group v-model="leftSearchForm.clientNames">
                            <el-checkbox v-for="item in dialog.content.showClients"
                                         :key="item.clientId"
                                         :label="item.clientName" border></el-checkbox>
                          </el-checkbox-group> -->
                          <el-select v-model="dialog.style.leftClientChecked" collapse-tags clearable multiple placeholder="请选择归属系统" ref="clientCheckLeft">
                            <el-option value="all" @click.native="checkAll">全选</el-option>
                            <el-option v-for="item in dialog.content.showClients"
                                              :key="item.clientId"
                                              :value="item.clientId"
                                              :label="item.clientName"></el-option>
                          </el-select>
                          <span  class="buttonStyle"><el-form-item>
                          <el-button type="primary" @click="leftRefreshTable"
                                     size="small"
                                     v-loading="leftLoading.search">查询
                          </el-button>
                        </el-form-item></span>
                        </el-form-item>
                      </el-col>
                      <!-- <el-col :span="2">
                        <el-form-item>
                          <el-button type="primary" @click="leftRefreshTable"
                                     v-loading="leftLoading.search">查询
                          </el-button>
                        </el-form-item>
                      </el-col> -->
                    </el-form>
                  </el-col>
                </el-row>
              </el-header>
              <el-table :data="leftTableData.data" highlight-current-row style="width: 100%" border>
                <el-table-column prop="RoleName" label="角色名称" show-overflow-tooltip></el-table-column>
                <el-table-column prop="ClientName" label="归属系统" show-overflow-tooltip></el-table-column>
                <el-table-column label="操作">
                  <template slot-scope="scope">
                    <el-button size="small" icon="el-icon-d-arrow-right"
                               @click="bindRole(scope.row)">
                    </el-button>
                  </template>
                </el-table-column>
              </el-table>
              <el-pagination background @size-change="leftPageSizeChangeHandler"
                             @current-change="leftPageNumberChangeHandler"
                             :page-sizes="leftTableData.pageList"
                             :page-size="leftTableData.pageSize"
                             :current-page="leftTableData.pageNumber"
                             layout="sizes, prev, pager, next"
                             :total="leftTableData.total">
              </el-pagination>
            </el-card>
          </el-col>
          <!--right-->
          <el-col :span="12">
            <el-card shadow="never">
              <div slot="header">
                <span>已分配角色</span>
              </div>
              <el-header>
                <el-row>
                  <el-col :span="24">
                    <el-form label-position="left" label-width="80px" :inline="refs.inline"
                             :model="rightSearchForm">
                      <el-col :span="24">
                        <el-form-item label="角色名称">
                          <el-input v-model="rightSearchForm.roleName"
                                    placeholder="请输入角色名称"
                                    style="width:212px"></el-input>
                        </el-form-item>
                      </el-col>
                      <el-col :span="24">
                        <el-form-item label="归属系统">
                          <!-- <el-checkbox-group v-model="rightSearchForm.clientNames">
                            <el-checkbox v-for="item in dialog.content.showClients"
                                         :key="item.clientId"
                                         :label="item.clientName" border></el-checkbox>
                          </el-checkbox-group> -->
                          <el-select v-model="dialog.style.rightClientChecked" collapse-tags clearable multiple placeholder="请选择归属系统" ref="clientCheckRight">
                            <el-option value="all" @click.native="checkAll">全选</el-option>
                            <el-option v-for="item in dialog.content.showClients"
                                              :key="item.clientId"
                                              :value="item.clientId"
                                              :label="item.clientName"></el-option>
                          </el-select>
                          <span  class="buttonStyle"><el-form-item>
                          <el-button type="primary" @click="rightRefreshTable"
                                     size="small"
                                     v-loading="rightLoading.search">查询
                          </el-button>
                        </el-form-item></span>
                        </el-form-item>
                      </el-col>
                      <!-- <el-col :span="2">
                        <el-form-item>
                          <el-button type="primary" @click="rightRefreshTable"
                                     v-loading="rightLoading.search">查询
                          </el-button>
                        </el-form-item>
                      </el-col> -->
                    </el-form>
                  </el-col>
                </el-row>
              </el-header>
              <el-table :data="rightTableData.data" highlight-current-row style="width: 100%">
                <el-table-column label="操作">
                  <template slot-scope="scope">
                    <el-button size="small" icon="el-icon-d-arrow-left"
                               @click="unbindRole(scope.row)">
                    </el-button>
                  </template>
                </el-table-column>
                <el-table-column prop="RoleName" label="角色名称"></el-table-column>
                <el-table-column prop="ClientName" label="归属系统"></el-table-column>
              </el-table>
              <el-pagination background @size-change="rightPageSizeChangeHandler"
                             @current-change="rightPageNumberChangeHandler"
                             :page-sizes="rightTableData.pageList"
                             :page-size="rightTableData.pageSize"
                             :current-page="rightTableData.pageNumber"
                             layout="sizes, prev, pager, next"
                             :total="rightTableData.total">
              </el-pagination>
            </el-card>
          </el-col>
        </div>
      </el-dialog>
    </el-col>
  </el-row>

</template>

<script>
  export default {
    props: {
      openPicker: {
        type: String,
        default: ''
      }
    },
    name: 'RolePicker',
    data() {
      return {
        border: true,
        dialog: {
          title: '分配角色',
          visible: false,
          content: {
            userId: '',
            showClients: []
          },
           style: {
             leftClientChecked: [],
             rightClientChecked: []
          }
        },
        refs: {
          dialog: 'dialog',
          inline: false,
          leftForm: 'form',
          rightForm: 'form'
        },
        leftLoading: {
          search: false,
          table: false
        },
        rightLoading: {
          search: false,
          table: false
        },
        leftSearchForm: {
          roleName: '',
          resource: '',
          pageNumber: 1,
          pageSize: 5,
          clients: '',
          // clientNames: [],
          userId: '',
          type: 'notExists',

        },
        rightSearchForm: {
          roleName: '',
          resource: '',
          pageNumber: 1,
          pageSize: 5,
          clients: '',
          clientNames: [],
          userId: '',
          type: 'exists',
          // rightStyle: {
          //   rightClientChecked: []
          // }
        },
        leftTableData: {
          pageSize: 5,
          pageList: [5, 10],
          pageNumber: 1,
          data: []
        },
        rightTableData: {
          pageSize: 5,
          pageList: [5, 10],
          pageNumber: 1,
          data: []
        }
      };
    },
    created() {
       this.leftRefreshTable();
       this.rightRefreshTable();
    },
    methods: {
       checkAll() {
        this.dialog.style.leftClientChecked = this.dialog.content.showClients.map( (item) => item.clientId);
        this.dialog.style.rightClientChecked = this. dialog.content.showClients.map( (item) => item.clientId);
        this.$refs.clientCheckLeft.blur();
        this.$refs.clientCheckRight.blur();
      },
      /**
       * 分页条数改变监听
       * @param{Number} pageSize
       */
      leftPageSizeChangeHandler(pageSize) {
        this.leftFetchTableData(pageSize, 1);
      },
      rightPageSizeChangeHandler(pageSize) {
        this.rightFetchTableData(pageSize, 1);
      },
      /**
       * 分页页码改变监听
       * @param{Number} pageNumber
       */
      leftPageNumberChangeHandler(pageNumber) {
        this.leftFetchTableData(this.leftTableData.pageSize, pageNumber);
      },
      rightPageNumberChangeHandler(pageNumber) {
        this.rightFetchTableData(this.leftTableData.pageSize, pageNumber);
      },
      leftFetchTableData(pageSize = this.leftTableData.pageSize,
          pageNumber = this.leftTableData.pageNumber) {
        this.leftSearchForm.clients = '';
        let len = this.dialog.content.showClients.length;
        let leng = this.dialog.style.leftClientChecked.length;
        for (let i = 0; i < len; i++) {
          for (let j = 0; j < leng; j++) {
            if (this.dialog.style.leftClientChecked[j]
                === this.dialog.content.showClients[i].clientId) {
              if (i === len - 1 || leng === 1) {
                this.leftSearchForm.clients += '\'' + this.dialog.content.showClients[i].clientId
                    + '\'';
              } else {
                this.leftSearchForm.clients += '\'' + this.dialog.content.showClients[i].clientId
                    + '\',';
              }
            }
          }
        }
        this.leftSearchForm.userId = this.dialog.content.userId;
        this.leftLoading.table = true;
        this.$api.roles.getRolesByPage(pageSize, pageNumber, this.leftSearchForm)
        .then((res) => {
          let data = res.data.data;
          this.leftTableData.data = data.list;
          this.leftTableData.total = data.total;
          this.leftTableData.pageSize = data.pageSize;
          this.leftTableData.pageNumber = data.pageNum;
          this.leftLoading.table = false;
          this.leftLoading.search = false;
        })
        .catch((err) => {
          this.$message({
            showClose: true,
            message: err.data.meta.message,
            type: 'error'
          });
        });
      },
      rightFetchTableData(pageSize = this.rightTableData.pageSize, pageNumber =
          this.rightSearchForm.pageNumber) {
        this.rightSearchForm.clients = '';
        let len = this.dialog.content.showClients.length;
        let leng = this.dialog.style.rightClientChecked.length;
        let k = 0;
        for (let i = 0; i < len; i++) {
          for (let j = 0; j < leng; j++) {
            if (this.dialog.style.rightClientChecked[j]
                === this.dialog.content.showClients[i].clientId) {
              k++;
              if (leng === 1 || k === leng) {
                this.rightSearchForm.clients += `'${this.dialog.content.showClients[i].clientId}'`;
              } else {
                this.rightSearchForm.clients += `'${this.dialog.content.showClients[i].clientId}',`;
              }
            }
          }
        }
        this.rightSearchForm.userId = this.dialog.content.userId;
        this.rightLoading.table = true;
        this.$api.roles.getRolesByPage(pageSize, pageNumber, this.rightSearchForm)
        .then((res) => {
          let data = res.data.data;
          this.rightTableData.data = data.list;
          this.rightTableData.total = data.total;
          this.rightTableData.pageSize = data.pageSize;
          this.rightTableData.pageNumber = data.pageNum;
          this.rightLoading.table = false;
          this.rightLoading.search = false;
        })
        .catch((err) => {
          this.$message({
            showClose: true,
            message: err.data.meta.message,
            type: 'error'
          });
        });
      },
      /**
       * 刷新表格内容
       */
      leftRefreshTable() {
        this.leftLoading.search = true;
        this.leftFetchTableData();
      },
      rightRefreshTable() {
        this.rightLoading.search = true;
        this.rightFetchTableData();
      },
      /**
       * 重置dialog信息
       */
      reset() {
        this.$refs[this.refs.leftForm].clearValidate();
        this.$refs[this.refs.leftForm].resetFields();

        this.$refs[this.refs.rightForm].clearValidate();
        this.$refs[this.refs.rightForm].resetFields();

        for (const key in this.leftSearchForm) {
          if (this.leftSearchForm.hasOwnProperty(key)) {
            this.leftSearchForm[key] = '';
          }
        }
        for (const key in this.rightSearchForm) {
          if (this.rightSearchForm.hasOwnProperty(key)) {
            this.rightSearchForm[key] = '';
          }
        }
        this.leftSearchForm.clientNames = [];
        this.leftSearchForm.type = 'notExists';
        this.leftSearchForm.pageSize = 5;
        this.leftSearchForm.pageNumber = 1;
        this.rightSearchForm.clientNames = [];
        this.rightSearchForm.type = 'exists';
        this.rightSearchForm.pageSize = 5;
        this.rightSearchForm.pageNumber = 1;
        this.dialog.content.userId = '';
        this.dialog.content.showClients = [];
      },
      /**
       * 关闭弹窗1
       */
      close() {
        this.reset();
        this.dialog.visible = false;
        this.$emit('closePicker');
      },
      /**
       * 分配角色
       * @param{Object} row
       */
      bindRole(row) {
        this.$api.users.addUserRole(this.dialog.content.userId, row.RoleId)
        .then((res) => {
          this.$message({
            showClose: true,
            message: res.data.meta.message,
            type: 'success'
          });
          Promise.all([this.rightRefreshTable(), this.leftRefreshTable()]).catch((err) => {
            console.error(err);
          });
        })
        .catch((err) => {
          this.$message({
            showClose: true,
            message: err.data.meta.message,
            type: 'error'
          });
        });
      },
      /**
       * 删去已分配角色
       * @param{Object} row
       */
      unbindRole(row) {
        this.$api.users.removeUserRole(this.dialog.content.userId, row.RoleId)
        .then((res) => {
          this.$message({
            showClose: true,
            message: res.data.meta.message,
            type: 'success'
          });
          Promise.all([this.rightRefreshTable(), this.leftRefreshTable()]).catch((err) => {
            console.error(err);
          });
        })
        .catch((err) => {
          this.$message({
            showClose: true,
            message: err.data.meta.message,
            type: 'error'
          });
        });
      },
      /**
       * 获取相关系统，查询左右两边table
       */
      afterOpen() {
        this.rightLoading.search = true;
        this.rightRefreshTable();
        this.leftLoading.search = true;
        this.leftRefreshTable();
        this.$api.users.getRelatedClients(this.dialog.content.userId).then((res) => {
          this.dialog.content.showClients = res.data.data;
          let len = this.dialog.content.showClients.length;
          for (let i = 0; i < len; i++) {
            this.dialog.style.leftClientChecked.push(this.dialog.content.showClients[i].clientName);
            this.dialog.style.rightClientChecked.push(this.dialog.content.showClients[i].clientName);
          }
          Promise.all([this.rightRefreshTable(), this.leftRefreshTable()]).catch((err) => {
            return Promise.reject(err);
          });
        })
        .catch((err) => {
          console.error(err);
          this.$message({
            showClose: true,
            message: err.data.meta.message,
            type: 'error'
          });
        });
      }
    },
    watch: {
      openPicker(userId) {
        if (userId !== '') {
          this.dialog.visible = true;
          this.dialog.content.userId = userId;
        } else {
          this.close();
        }
      }
    }
  };
</script>
<style >
.el-table th{
   background-color:#e8f1f9;
   font-weight: 545;
   text-align: center;
}
.el-table td{
    text-align: center;
}
.buttonStyle {
  float: right;
  margin-right: 20px;
  margin-top: -5px;
}
.el-dialog__header {
    padding: 10px 20px 10px;
    margin-left: 45px;
    margin-top: 10px;
    border: 1px solid #EBEEF5;
    width: 91%;
}
.el-dialog__headerbtn .el-dialog__close {
    color: #909399;
    margin-right: 40px;
}

</style>
