<template>
  <el-dialog :title="dialog.title" :visible.sync="dialog.visible" :fullscreen="true"
             :ref="refs.dialog" @close="close"
             width="50%">
    <el-form :model="dialog.content" :rules="dialog.rules" @submit.native.prevent :ref="refs.form"
             label-width="100px">
      <el-form-item label="账号" prop="loginAcct">
        <el-input v-model="dialog.content.loginAcct" placeholder="请输入账号"></el-input>
      </el-form-item>
      <el-form-item label="用户名" prop="nickName">
        <el-input v-model="dialog.content.nickName" placeholder="请输入用户名"></el-input>
      </el-form-item>
      <el-form-item label="初始密码" prop="password" v-show="dialog.passwordShow">
        <el-input v-model="dialog.content.password"></el-input>
      </el-form-item>
      <el-form-item label="账号类型" prop="accountType">
        <el-radio-group v-model="dialog.content.accountType">
          <el-radio v-for="item of dialog.accountTypes" :key="item.VALUE" :label="item.VALUE">
            {{item.TEXT}}
          </el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="是否游客" prop="isGuest">
        <el-switch v-model="dialog.content.isGuest" active-color="#13ce66" inactive-color="#fff6f6"
                   active-value="Y"
                   inactive-value="N"
                   @change="showHrEmpee()">
        </el-switch>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="onSubmit">提交</el-button>
        <el-button @click="reset">重置</el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>

<script>

  export default {
    name: 'UserDialog',
    props: {
      'openAddDialog': {
        type: Boolean,
        default: false
      },
      'openEditDialog': {
        type: String,
        default: ''
      }
    },
    computed: {
      trimmedFormContent: (vm) => {
        return Object.assign({}, vm.dialog.content, {
          loginAcct: vm.dialog.content.loginAcct.trim(),
          nickName: vm.dialog.content.nickName.trim()
        });
      }
    },
    data() {
      return {
        refs: {
          dialog: 'dialog',
          form: 'form'
        },
        dialog: {
          title: '',
          visible: false,
          content: {
            userId: '',
            loginAcct: '',
            nickName: '',
            password: '',
            accountType: '1',
            isGuest: 'N',
            hrEmpeeId: ''
          },
          accountTypes: [],
          hrEmpeeIdShow: false,
          passwordShow: true,
          rules: {
            loginAcct: [{
              required: true,
              trigger: 'blur',
              validator: (rule, value, callback) => {
                if (!this.dialog.content.loginAcct) {
                  callback(new Error('请输入用户账号'));
                } else
                if(this.dialog.content.loginAcct
                    && /[\u4e00-\u9fa5]+/.test(this.dialog.content.loginAcct)){
                  callback(new Error('请输入非中文字符'));
                } else {
                  callback();
                }
              }
            }],
            nickName: [{
              required: true,
              message: '用户名不能为空',
              trigger: 'blur'
            }],
            accountType: [{
              required: true,
              message: '账号类型不能为空',
              trigger: 'blur'
            }]
          }
        }
      };
    },
    methods: {
      /**
       * 弹出新增页面
       */
      showAddUserDialog() {
        Promise.all([
          this.$api.users.getDefaultPassword(), // 获取默认密码
          this.$api.users.getAccountTypes() // 获取账号类型
        ]).then(([passwordData, accTypesData]) => {
          this.dialog.accountTypes = accTypesData.data.data;
          this.dialog.content.password = passwordData.data.meta.message;
          this.dialog.content.isGuest = 'N';
          this.dialog.title = '新增';
          this.dialog.visible = true;
          this.dialog.content.accountType = '1'
        })
        .catch((err) => {
          this.$message({
            showClose: true,
            message: err.data.meta.message,
            type: 'error'
          });
        });
      },
      /**
       * 弹出修改页面
       * @param{String} userId
       */
      showEditUserDialog(userId) {
        this.dialog.content.userId = userId;
        Promise.all([
          this.$api.users.getUserByUserId(this.dialog.content.userId), // 获取用户信息
          this.$api.users.getAccountTypes() // 获取账户信息
        ])
        .then(([resUser, accTypes]) => {
          this.dialog.accountTypes = accTypes.data.data;
          this.dialog.content = resUser.data.data;
          this.dialog.passwordShow = false;
          if (this.dialog.content.isGuest === 'N') {
            this.dialog.hrEmpeeIdShow = true;
          }
          this.dialog.title = '编辑';
          this.dialog.visible = true;
        })
        .catch((err) => {
          this.$message({
            showClose: true,
            message: err.data.meta.message,
            type: 'error'
          });
        });
      },
      /**
       * (dialog)是否显示hr员工
       */
      showHrEmpee() {
        if (this.dialog.content.isGuest === 'N') {
          this.dialog.hrEmpeeIdShow = true;
        } else {
          this.dialog.hrEmpeeIdShow = false;
          this.dialog.content.hrEmpeeId = '';
        }
      },
      reset() {
        // 清除校验信息
        this.$refs[this.refs.form].clearValidate();
        // 重置表单
        this.$refs[this.refs.form].resetFields();
        // 清楚vue实例中的表单数据
        for (const key in this.dialog.content) {
          if (this.dialog.content.hasOwnProperty((key))) {
            this.dialog.content[key] = '';
          }
        }
        // 移除HR员工栏
        this.dialog.hrEmpeeIdShow = false;
        this.dialog.content.isGuest = 'N';
        this.dialog.passwordShow = true;
      },
      /**
       * dialog的关闭回调
       */
      close() {
        this.reset();
        this.$emit('closeDialog');
      },
      /**
       * 新增（修改）提交
       */
      onSubmit() {
        this.$refs[this.refs.form].validate((valid) => {
          if (valid) {
            let promise = this.dialog.content.userId === '' ?
                this.$api.users.addUser(this.trimmedFormContent) :
                this.$api.users.editUser(this.trimmedFormContent);

            promise.then((response) => {
              if (response.data.meta.success === false) {
                this.$message({
                  dangerouslyUseHTMLString: true,
                  showClose: true,
                  message: response.data.meta.message,
                  type: 'error'
                });
              } else {
                this.$message({
                  showClose: true,
                  message: response.data.meta.message,
                  type: 'success'
                });
                this.dialog.visible = false;
                this.$emit('refreshTable');
              }
            })
            .catch((error) => {
              this.$message({
                dangerouslyUseHTMLString: true,
                showClose: true,
                message: error.meta.message,
                type: 'error'
              });
            });
          }
        });
      }
    },
    watch: {
      openAddDialog(boolean) {
        if (boolean) {
          this.showAddUserDialog();
        } else {
          this.close();
        }
      },
      openEditDialog(userId) {
        if (userId !== '') {
          this.showEditUserDialog(userId);
        } else {
          this.close();
        }
      }
    }
  };
</script>

<style scoped>

</style>
