<template>
  <div style="height: 100%;overflow: auto">
    <el-row>
      <el-col :span="24">
        <el-form :inline="true" :model="searchForm">
          <el-form-item label="账号">
            <el-input v-model="searchForm.loginAcct" placeholder="请输入账号"></el-input>
          </el-form-item>
          <el-form-item label="用户名">
            <el-input v-model="searchForm.nickName" placeholder="请输入用户名"></el-input>
          </el-form-item>
          <el-form-item label="状态">
            <el-select v-model="searchForm.state">
              <el-option v-for="item in userStateList" :key="item.VALUE" :label="item.TEXT"
                         :value="item.VALUE">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="refreshTable" v-loading="loading.search">查询
            </el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="add" v-shiro:hasPermission="$previlegeCodes.users.add">
              新增账号
            </el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col :span="24">
        <el-table :data="tableData.data" highlight-current-row v-loading="loading.table"
                  style="width: 100%" border show-overflow-tooltip>
          <el-table-column prop="loginAcct" label="账号" show-overflow-tooltip></el-table-column>
          <el-table-column prop="nickName" label="用户" show-overflow-tooltip></el-table-column>
          <el-table-column prop="accountTypeName" label="账号类型"></el-table-column>
          <el-table-column prop="isGuestName" label="是否游客"></el-table-column>
          <el-table-column prop="regSourceName" label="注册来源" show-overflow-tooltip></el-table-column>
          <el-table-column prop="stateName" label="状态"></el-table-column>
          <el-table-column fixed="right" min-width="340px" label="操作">
            <template slot-scope="scope">
              <el-button type="text" size="mini" v-if="scope.row.state === '10A'"
                         @click="edit(scope.row)"
                         v-shiro:hasPermission="$previlegeCodes.users.edit">编辑
              </el-button>
              <el-button type="text" size="mini" v-if="scope.row.state === '10A'"
                         @click="resetPassword(scope.row)"
                         v-shiro:hasPermission="$previlegeCodes.users.reset">重置密码
              </el-button>
              <el-button type="text" size="mini" v-if="scope.row.state === '10A'"
                         @click="distributeRole(scope.row)"
                         v-shiro:hasPermission="$previlegeCodes.users.bindRole">分配角色
              </el-button>
              <el-button type="text" size="mini" v-if="scope.row.state === '10A'"
                         @click="freeze(scope.row)"
                         v-shiro:hasPermission="$previlegeCodes.users.freeze">冻结
              </el-button>
              <el-button type="text" size="mini" v-if="scope.row.state === '10A'"
                         @click="cancel(scope.row)"
                         v-shiro:hasPermission="$previlegeCodes.users.cancel">注销
              </el-button>
              <el-button type="text" size="mini" v-if="scope.row.state === '10F'"
                         @click="unfreeze(scope.row)"
                         v-shiro:hasPermission="$previlegeCodes.users.unfreeze">解冻
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination background @size-change="pageSizeChangeHandler"
                       @current-change="pageNumberChangeHandler"
                       :page-sizes="tableData.pageList"
                       :page-size="tableData.pageSize" :current-page="tableData.pageNumber"
                       layout="sizes, prev, pager, next" :total="tableData.total">
        </el-pagination>
      </el-col>
      <el-col :span="24" style="margin-top: 10px;">

        <!--用户信息弹窗-->
        <user-dialog :openAddDialog="dialogTools.showAddDialog"
                     :openEditDialog="dialogTools.userId"
                     @closeDialog="closeDialog"
                     @refreshTable="refreshTable">
        </user-dialog>
        <!--分配角色-->
        <role-picker :openPicker="dialogTools.rolePickerUserId"
                     @closePicker="closePicker">
        </role-picker>
      </el-col>
    </el-row>
  </div>

</template>
<script>
  import constants from '@common/constants/constants';
  import RolePicker from '@components/users/RolePicker';
  import UserDialog from '@components/users/UserDialog';

  export default {
    components: {
      'role-picker': RolePicker,
      'user-dialog': UserDialog
    },

    name: 'UserSection',
    data() {
      return {
        border: true,
        dialogTools: {
          showAddDialog: false,
          userId: '',
          rolePickerUserId: ''
        },
        searchForm: {
          loginAcct: '',
          nickName: '',
          state: '',
          role: ''
        },
        userStateList: [],
        tableData: {
          pageSize: constants.PAGE_SIZE,
          pageList: constants.PAGE_LIST,
          pageNumber: 1,
          data: []
        },
        loading: {
          search: false,
          table: false
        }
      };
    },
    methods: {
      /**
       * 关闭角色信息弹窗
       */
      closePicker() {
        this.dialogTools.rolePickerUserId = '';
      },
      /**
       * 关闭角色信息弹窗
       */
      closeDialog() {
        this.dialogTools.showAddDialog = false;
        this.dialogTools.userId = '';
      },
      /**
       * 打开新增弹窗
       */
      add() {
        this.dialogTools.showAddDialog = true;
      },
      /**
       * 打开修改弹窗
       * @param{Object}row
       */
      edit(row) {
        this.dialogTools.userId = row.userId;
      },
      /**
       * 分页条数改变监听
       * @param{Number} pageSize
       */
      pageSizeChangeHandler(pageSize) {
        this.fetchTableData(pageSize, 1);
      },
      /**
       * 分页页码改变监听
       * @param{Number} pageNumber
       */
      pageNumberChangeHandler(pageNumber) {
        this.fetchTableData(this.tableData.pageSize, pageNumber);
      },
      fetchTableData(pageSize = this.tableData.pageSize, pageNumber = this.tableData.pageNumber) {
        this.loading.table = true;
        this.$api.users.getUserByPage(pageSize, pageNumber, this.searchForm)
        .then((res) => {
          let data = res.data.data;
          this.tableData.data = data.list;
          this.tableData.total = data.total;
          this.tableData.pageSize = data.pageSize;
          this.tableData.pageNumber = data.pageNum;
          this.loading.table = false;
          this.loading.search = false;
        })
        .catch((err) => {
          this.$message({
            showClose: true,
            message: err.data.meta.message,
            type: 'error'
          });
        });
      },
      /**
       * 刷新表格内容
       */
      refreshTable() {
        this.loading.search = true;
        this.fetchTableData(this.tableData.pageSize, 1);
      },
      /**
       * 密码重置
       * @param{Object} row
       */
      resetPassword(row) {
        let tip = `即将为【${row.nickName}】重置密码，是否确认？`;
        this.$confirm(tip, {
          type: 'warning'
        })
        .then(() => {
          this.$api.users.resetPassword(row.userId)
          .then((res) => {
            this.$message({
              showClose: true,
              message: res.data.meta.message,
              type: 'success'
            });
          })
          .catch((err) => {
            this.$message({
              showClose: true,
              message: err.data.meta.message,
              type: 'error'
            });
          });
        });
      },
      /**
       * 分配角色（获取角色列表）
       * @param{Object} row
       */
      distributeRole(row) {
        this.dialogTools.rolePickerUserId = row.userId;
      },
      /**
       * 冻结用户
       * @param{Object} row
       */
      freeze(row) {
        let tip = `即将冻结【${row.nickName}】，是否确认？`;
        this.$confirm(tip, {
          type: 'warning'
        })
        .then(() => {
          this.$api.users.freeze(row.userId)
          .then((res) => {
            this.fetchTableData();
            this.$message({
              showClose: true,
              message: res.data.meta.message,
              type: 'success'
            });
          })
          .catch((err) => {
            this.$message({
              showClose: true,
              message: err.data.meta.message,
              type: 'error'
            });
          });
        });
      },
      /**
       * 解冻
       * @param{Object} row
       */
      unfreeze(row) {
        let tip = `即将解冻【${row.nickName}】，是否确认？`;
        this.$confirm(tip, {
          type: 'warning'
        })
        .then(() => {
          this.$api.users.unfreeze(row.userId)
          .then((res) => {
            this.fetchTableData();
            this.$message({
              showClose: true,
              message: res.data.meta.message,
              type: 'success'
            });
          })
          .catch((err) => {
            this.$message({
              showClose: true,
              message: err.data.meta.message,
              type: 'error'
            });
          });
        });
      },
      /**
       * 注销
       * @param{Object} row
       */
      cancel(row) {
        let tip = `即将注销【${row.nickName}】，是否确认？`;
        this.$confirm(tip, {
          type: 'warning'
        })
        .then(() => {
          this.$api.users.cancel(row.userId)
          .then((res) => {
            this.fetchTableData();
            this.$message({
              showClose: true,
              message: res.data.meta.message,
              type: 'success'
            });
          })
          .catch((err) => {
            this.$message({
              showClose: true,
              message: err.data.meta.message,
              type: 'error'
            });
          });
        });
      }
    },
    created() {
      // 获取用户账户状态数据字典
      this.$api.users.getUserStates().then((res) => {
        let data = res.data.data;
        this.userStateList = data;
        this.searchForm.state = data[0].VALUE;
        // 获取表格内容
        this.fetchTableData();
      }).catch((err) => {
        this.$message({
          showClose: true,
          message: err.data.meta.message,
          type: 'error'
        });
      });
    }
  };

</script>
<style >
.el-table th{
   background-color:#e8f1f9;
   font-weight: 545;
   text-align: center;
}
.el-table td{
    text-align: center;
}

</style>

